
.trending-post-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    margin: 10px 0;
    margin-left: auto;
  }
  h1{
    font-weight: 600;
    text-align: center;
  }
  
  .trending-post-main {
    flex-basis: 50%;
    display: flex;
    justify-content: center;
  }
  
  .main-image {
    width: 90%;
    height: auto;
  }
  
  .trending-post-side {
    flex-basis: 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 5px;

  }
  
  .side-image {
    width: 60%;
    height: 35%;
    object-fit: cover;
    margin-top: 5px;
  }
  