
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: white;
    color: rgb(169, 6, 6);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: relative;
    z-index: 10;
  }
  
  .navbar-left {
    display: flex;
    align-items: center;
  }
  
  .logo {
    font-size: 24px;
    font-weight: bold;
    margin-right: 20px;
    cursor: pointer;
    transition: transform 0.3s ease;
  }
  
  .logo:hover {
    transform: scale(1.1);
  }
  
  .dropdown {
    position: relative;
  }
  
  .dropdown-btn {
    background-color: #ffffff;
    color: rgb(142, 67, 67);
    padding: 20px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .dropdown-btn:hover {
    background-color: #edf0e6;
  }
  
  .dropdown-content {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 40px;
    left: 0;
    background-color: #8b4444;
    box-shadow: 0 4px 8px rgb(255, 254, 254);
    border-radius: 4px;
    overflow: hidden;
    z-index: 5;
    opacity: 0;
    animation: fadeIn 0.3s forwards;
  }
  
  @keyframes fadeIn {
    to {
      opacity: 1;
    }
  }
  
  .dropdown-content a {
    padding: 10px 20px;
    color: white;
    text-decoration: none;
    transition: background-color 0.3s ease;
  }
  
  .dropdown-content a:hover {
    background-color: #555;
  }
  
  .navbar-right {
    display: flex;
    align-items: center;
  }
  
  .signin-btn {
    background-color: #555;
    color: white;
    border: none;
    padding: 10px;
    margin-right: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .signin-btn:hover {
    background-color: #666;
  }
  
  .search-bar {
    padding: 10px;
    border-radius: 4px;
    border:1px solid black ;
    outline:#555;
    transition: width 0.3s ease;
    width: 150px;
  }
  
  .search-bar:focus {
    width: 200px;
  }
  