
.latest-post-container {
    width: 60%;
    margin: 0 auto;
    padding: 20px 0;
    
  }
  
  .section-title {
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: bold;
  }
  
  .filter-bar {
    
    color:navy ;
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 20px;
  }
  
  .filter-buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
  }
  
  .filter-button {
    border: 2px solid blue;
    background-color: white;
    color: navy;
    padding: 10px 20px;
    border-radius: 50px;
    cursor: pointer;
    transition: transform 0.3s ease, background-color 0.3s ease, color 0.3s ease;
  }
  
  .filter-button:hover {
    background-color: navy;
    color: white;
    transform: scale(1.1);
  }
  