
.footer {
    display: flex;
    flex-wrap: wrap;
    background-color: #2c3e50;
    color: white;
    padding: 40px 20px;
    justify-content: space-between;
  }
  
  .footer-column {
    flex: 1;
    margin: 10px;
  }
  
  .footer-column h4 {
    margin-bottom: 15px;
    font-size: 18px;
  }
  
  .footer-column ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-column ul li {
    margin-bottom: 10px;
    font-size: 14px;
  }
  
  .footer-column address {
    font-style: normal;
    line-height: 1.6;
  }
  
  .footer-column a {
    color: #3498db;
    text-decoration: none;
    display: block;
    margin-bottom: 10px;
  }
  
  .footer-column a:hover {
    text-decoration: underline;
  }
  
  .social-icons span {
    display: inline-block;
    margin-right: 10px;
    cursor: pointer;
    color: #3498db;
  }
  
  .social-icons span:hover {
    color: #2980b9;
  }
  